<template>
  <div class="homenav">
    <van-row>
      <van-col span="4"
        ><van-dropdown-menu>
          <van-dropdown-item title="搜索" ref="item">
            <!-- 搜索组件 -->
            <search-item></search-item>
          </van-dropdown-item>
        </van-dropdown-menu>
      </van-col>

      <van-col span="10" offset="3">
        <span class="title">正在获取地址.....</span>
      </van-col>
      <van-col span="7">
        <van-nav-bar>
          <template #right>
            <a @click="checkLogin">{{ account }}</a>
          </template>
        </van-nav-bar>
      </van-col>
    </van-row>
  </div>
</template>


<script>
import { DropdownMenu, DropdownItem } from "vant";
import SearchItem from "./SearchItem.vue";
export default {
  name: "HomeNav",
  data() {
    return {
      account: "登录|注册",
      SearchValue: "",
    };
  },
  created() {
    var state = localStorage.getItem("account");
    if (state == "" || state ==null) {
    } else {
      this.account = "个人中心";
    }
  },
  components: {
    SearchItem,
    [DropdownMenu.name]: DropdownMenu,
    [DropdownItem.name]: DropdownItem,
  },
  methods: {
    checkLogin() {
      if (this.account == "登录|注册") {
        this.$router.push({ name: "Login" });
      } else {
        this.$router.push({ name: "Mine" });
      }
    },
  },
};
</script>

<style>
.homenav .van-nav-bar__title {
  margin-right: 32%;
}
.homenav .van-nav-bar__content {
  height: 48px;
}
.homenav .van-dropdown-menu__bar,
.homenav .van-nav-bar,
.homenav .van-row {
  background-color: #4fc08d;
}
.homenav .van-dropdown-menu__title,
.homenav .van-dropdown-menu__title--active {
  color: #fff;
}
.homenav .van-nav-bar__right a {
  color: #fff;
  font-size: 15px;
}
.homenav .title {
  color: #fff;
  font-size: 15px;
  height: 48px;
  display: inline-block;
  line-height: 48px;
}
</style>


