<template>
  <div class="login-cellgroup">
    <van-form @submit="onSubmit">
      <van-field
        v-model="username"
        name="用户名"
        placeholder="用户名"
        :rules="[{ required: true, message: '请填写用户名' }]"
      />
      <van-field
        v-model="account"
        name="账号"
        placeholder="账号"
        :rules="[{ required: true, message: '请填写账号' }]"
      />
      <van-cell class="login-pwdcell">
        <van-field
          id="re_pwd"
          v-model="password"
          type="password"
          name="密码"
          placeholder="密码"
          :rules="[{ required: true, message: '请填写密码' }]"
        />

        <template #right-icon>
          <van-switch v-model="checked" size="24" @click="showPwd" />
        </template>
      </van-cell>

      <div style="margin: 16px">
        <van-button round block type="info" native-type="submit"
          >提交</van-button
        >
      </div>
    </van-form>
  </div>
</template>

<script>
import { Notify } from "vant";
export default {
  name: "RegisterPwd",
  data() {
    return {
      username: "",
      account: "",
      password: "",
      checked: false,
    };
  },
  components: {
    [Notify.name]: Notify,
  },
  methods: {
    onSubmit(values) {
      // console.log("submit", values);
      var app = this;
      var params = {
        name: this.username,
        account: this.account,
        password: this.password,
      };
      this.$http.post("/user/register", params).then(function (res) {
        switch (res.data) {
          case "register success":
            Notify({ type: "success", message: "注册成功", duration: "2000" });
            // 注测成功后 ，使用事件总线将至传递给登录，然后将注册好的账号密码填入登录界面
            localStorage.setItem("regAcc", app.account);
            localStorage.setItem("regPwd", app.password);
            localStorage.setItem("account", app.username);
            app.$store.commit("changeReg",true)
            setTimeout(() => {
              //1秒后刷新界面
              app.$router.go(0);
            }, 1000);
            break;
          case "register fail":
            Notify({ type: "danger", message: "注册失败", duration: "2000" });
            app.clearFrom();
            break;
          case "account is exit":
            Notify({
              type: "warning",
              message: "用户已存在",
              duration: "2000",
            });
            app.clearFrom();
            break;
          default:
            break;
        }
      });
    },
    //显示密码
    showPwd() {
      if (this.checked) {
        document.getElementById("re_pwd").type = "password";
        this.checked = false;
      } else {
        document.getElementById("re_pwd").type = "text";
        this.checked = true;
      }
    },
    //登陆失败时清楚输入内容
    clearFrom() {
      this.username = "";
      this.account = "";
      this.password = "";
      this.checked = false;
    },
  },
};
</script>

<style>
.login-cellgroup {
  padding: 5rem;
}
.login-cellgroup .van-cell {
  border: 1px solid #dddddd;
  border-radius: 10px;
  margin: 15px 0;
}
.login-cellgroup .login-pwdcell .van-cell {
  margin: 0;
  border: none;
  padding: 0;
}
</style>


