<template>
  <div class="Merchant">
    <!-- 头部 -->
    <merchant-title></merchant-title>
    <!-- 分页栏 -->
    <merchant-tab></merchant-tab>
  </div>
</template>
<script>
import MerchantTab from "../components/MerchantTab.vue";
import MerchantTitle from "../components/MerchantTitle.vue";
export default {
  name: "Merchant",
  data() {
    return {
      isMerchant:true,
    };
  },
  created() {
    this.$store.commit("getBusid", this.businessid);
    this.getInfoVuex();
    this.$store.commit("changeCartNum",0);
    this.$store.commit("changeBar",false);
  },
  props: ["businessid"],
  components: {
    MerchantTitle,
    MerchantTab,
  },
  methods: {
    getInfoVuex() {
      this.$store.dispatch("getBusInfo");
      this.$store.dispatch("getSpecialOffer");
      this.$store.dispatch("getAllFood");
      this.$store.dispatch("getFoodCategory");
      this.$store.dispatch("getBusEva");
    },
  },
};
</script>

