<template>
  <div>
    <van-search
      v-model="searchtext"
      show-action
      shape="round"
      placeholder="请输入搜索关键词"
      @search="onSearch"
    >
      <template #action>
        <div @click="onSearch">搜索</div>
      </template>
    </van-search>
  </div>
</template>
<script>
export default {
  name: "SearchItem",
  data() {
    return {
      searchtext: "",
    };
  },
  methods: {
    onSearch() {
      this.$store.commit("getSearchName", this.searchtext);
      this.$store.dispatch("searchbus");
    },
  },
  computed: {
    data() {
      return this.$store.state.allbus;
    },
  },
};
</script>
