<template>
  <div class="oLogin">
    <van-tabs active="0" sticky>
      <van-tab title="外卖订单">
        <div>  
          <div v-for="(item, index) in orders" :key="index">
            <van-card
              :price="
                item.list['0'].foodName + '等' + item.list.length + '件商品'
              "
              :title="item.shopName + '   >'"
              :thumb="imageUrl + item.list['0'].foodPhoto"
              currency=""
            >
              <template #footer>
                <van-button size="mini" round @click="removeOrder(item.id)">
                  删除订单
                </van-button>
                <van-button size="mini" round class="ol-add"
                  >再来一单</van-button
                >
              </template>
            </van-card>
          </div>
        </div>
        <!-- <van-empty description="订单为空" v-else /> -->
      </van-tab>

      <van-tab title="待评价" disabled></van-tab>
      <van-tab title="退款" disabled></van-tab>
      <van-tab title="其他订单" disabled></van-tab>
    </van-tabs>
  </div>
</template>
<script>
import { Empty } from "vant";
export default {
  name: "Order",
  data() {
    return {
      imageUrl: "http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/",
      empty: true,
    };
  },
  components: {
    [Empty.name]: Empty,
  },
  created() {
    this.getOrderVuex();
  },
  methods: {
    getOrderVuex() {
      this.$store.dispatch("getOrder");
    },
    removeOrder(id) {
      this.$store.state.orderid = id;
      this.$store.dispatch("removeOrder");
      this.$store.dispatch("getOrder");
      // this.$router.push({name:"Home"});
    },
  },
  computed: {
    orders() {
      // if (this.$store.state.orders == "") {
      //   this.empty = false;
      // } else {
        return this.$store.state.orders;
      // }
    },
  },
};
</script>
<style>
.oLogin .van-card__title {
  max-height: 32px;
  font-weight: bolder;
  font-size: 20px;
  line-height: inherit;
}
.oLogin .van-card__content {
  text-align: left;
}
.oLogin .ol-add {
  color: #099fe9;
  border: 1px solid #099fe9;
}
</style>