import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    allbus: [],
    searchname: "",
    searchbus: [],
    userid: 1,
    isReg:false,
    showBar:true,
    orders: [],
    orderid: 1,
    businessid: 1, //商家id
    busInfo: [], //商家信息
    busEva: [], //商家评语
    shopid: 1,
    foodCategory: [], // 食品分类信息
    foodName: "折扣",
    allFood: [], //所有商品
    special: [], //优惠信息
    cartNum: 0,
    cartList: { list: [] },
    orderList: []
  },
  mutations: {
    changeData(state, obj) {
      state.allbus = obj;
    },
    getSearchName(state, obj) {
      state.searchname = obj;
    },
    changeOrder(state, obj) {
      state.orders = obj;
    },
    changeUserid(state, obj) {
      state.userid = obj;
    },
    changeFoodCategory(state, obj) {
      state.foodCategory = obj;
    },
    changeAllFood(state, obj) {
      state.allFood = obj;
    },
    getBusid(state, obj) {
      state.businessid = obj;
    },
    getShopid(state, obj) {
      state.shopid = obj;
    },
    getFoodName(state, obj) {
      state.foodName = obj;
    },
    changeShopInfo(state, obj) {
      state.busInfo = obj;
    },
    changeShopEva(state, obj) {
      state.busEva = obj;
    },
    changeSpecial(state, obj) {
      state.special = obj;
    },
    changeCartNum(state, obj) {
      if (obj == "add") {
        state.cartNum++;
      } else if (obj == "red") {
        state.cartNum--;
      } else {
        state.cartNum = obj;
        state.cartList.list = [];
        state.orderList=[];
      }
    },
    addCartList(state, obj) {
      state.cartList.list.push(obj);
      var item = state.cartList.list;
      for (let index = 0; index < item.length; index++) {
        state.orderList.push({
          foodId: item[index].foodId,
          buyCount: item[index].buyCount,
          shopId: state.shopid
        });
      }
    },
    addBuyCount(state, obj) {
      var item = state.cartList.list;
      for (let index = 0; index < item.length; index++) {
        if (obj == item[index].foodId) {
          item[index].buyCount++;
          state.orderList[index].buyCount++;
          item[index].price = item[index].buyCount * 9;
        }
      }
    },
    redBuyCount(state, obj) {
      var item = state.cartList.list;
      for (let index = 0; index < item.length; index++) {
        if (obj == item[index].foodId) {
          item[index].buyCount--;
          state.orderList[index].buyCount--;
          item[index].price = item[index].buyCount * 9;
        }
      }
    },
    changeReg(state,obj){
      console.log(obj);
      state.isReg=obj;
    },
    changeBar(state,obj){
      state.showBar = obj;
    }
  },

  actions: {
    //获取全部商品 bussiness
    allbus(context) {
      axios
        .get("http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/biz/queryAllShopsInfo")
        .then(res => {
          context.commit("changeData", res.data);
        });
    },
    //根据搜索内容搜索商品
    searchbus(context) {
      axios
        .get(
          "http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/biz/queryAllShopsInfoByName?name=" +
            context.state.searchname
        )
        .then(res => {
          context.commit("changeData", res.data);
        });
    },
    // 获取订单信息
    getOrder(context) {
      axios
        .get(
          "http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/biz/queryOrdersByUserId?userId=" +
            context.state.userid
        )
        .then(res => {
          context.commit("changeOrder", res.data);
        });
    },
    removeOrder(context) {
      axios
        .get(
          "http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/biz/deleteOrderByOid?orderId=" +
            context.state.orderid
        )
        .then(res => {
          // context.orders=[]
          console.log(context.state.orderid);
          console.log(res.data);
        });
    },
    // 查询每一个商家的食品分类信息 get
    getFoodCategory(context) {
      axios
        .get(
          "http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/biz/queryFoodCategory?id=" +
            context.state.businessid
        )
        .then(res => {
          context.commit("changeFoodCategory", res.data);
        });
    },
    //查询每个商家中的所有食品 get
    getAllFood(context) {
      axios
        .get(
          "http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/biz/queryFoodinfoByShopIdAndFoodCategoryId?shopId=" +
            context.state.businessid +
            "+&foodcategoryId=" +
            context.state.shopid
        )
        .then(res => {
          context.commit("changeAllFood", res.data);
        });
    },
    // 通过shopid查询商家基本信息 get
    getBusInfo(context) {
      axios
        .get(
          "http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/biz/queryInfoByShopId?shopId=" +
            context.state.businessid
        )
        .then(res => {
          context.commit("changeShopInfo", res.data);
        });
    },
    // 查询每个商家评语 get
    getBusEva(context) {
      axios
        .get(
          "http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/biz/queryCommentByShopId?shopId=" +
            context.state.businessid
        )
        .then(res => {
          context.commit("changeShopEva", res.data);
        });
    },
    // 查询每个商家优惠活动 get
    getSpecialOffer(context) {
      axios
        .get(
          "http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/biz/querySpecialOfferByShopId?shopId=" +
            context.state.businessid
        )
        .then(res => {
          context.commit("changeSpecial", res.data);
        });
    },
    // 新增訂單
    insertOrder(context) {
      var params = {
        userId: context.state.userid,
        shopId: context.state.shopid,
        list: context.state.orderList
      };
      axios
        .post("http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/biz/insertOrder", params)
        .then(res => {
          console.log(res.data);
        });
    }
  },
  modules: {}
});
