<template>
  <!-- 点餐组件 -->
  <div class="mt-Order">
    <van-row>
      <van-col span="6">
        <van-sidebar v-model="activeKey">
          <van-sidebar-item
            v-for="(item, index) in foodCategory"
            :key="index"
            :title="item.name"
            @click="handleOne(item.id, item.name)"
          />
        </van-sidebar>
      </van-col>
      <van-col span="18">
        <router-view />
      </van-col>
    </van-row>

    <!-- 底部购物车 -->
    <van-submit-bar
      :price="allPrice*100"
      label=" "
      suffix-label="另需配送费5元"
      text-align="left"
      button-text="结算"
      @submit="onSubmit"
    >
      <!-- 购物车图标按钮 -->
      <template #default>
        <van-badge :content="cartNum" max="9" class="mto-Cart">
          <van-button
            id="CartBtn"
            round
            @click="showPopup"
            icon="cart"
            style="
              border: 5px solid #161d26;
              top: -10px;
              left: 5px;
              color: #fff;
              background: rgb(112, 203, 118);
            "
          >
          </van-button>
        </van-badge>
      </template>
      <!-- 价格 -->
    </van-submit-bar>

    <van-popup v-model="show" position="bottom">
      <div class="shopCart">
        <van-row class="CartTitle">购物车</van-row>
        <div class="CartCon">
          <van-list>
            <van-cell v-for="item in cartLIst.list" :key="item.name">
              <template #default class="content">
                <span>{{ item.name }}</span>
                <span>
                  <span>￥{{ item.price }}</span>
                  <van-stepper
                    @plus="addNum(item.foodId)"
                    @minus="reduceNum(item.foodId)"
                    v-model="item.buyCount"
                    theme="round"
                    button-size="18"
                    disable-input
                    style="float: right"
                  />
                </span>
              </template>
            </van-cell>
          </van-list>
        </div>
      </div>
    </van-popup>
  </div>
</template>
<script>
import { SubmitBar, Badge, Sidebar, SidebarItem } from "vant";
export default {
  name: "MerchantTabOrder",
  data() {
    return {
      activeKey: 0,
      show: false,
      cartList: { list: [] },
    };
  },
  created() {},
  components: {
    [Sidebar.name]: Sidebar,
    [SidebarItem.name]: SidebarItem,
    [SubmitBar.name]: SubmitBar,
    [Badge.name]: Badge,
  },
  methods: {
    onSubmit() {
      // console.log(this.$store)
      // 
      this.$store.dispatch("insertOrder");
      this.$store.dispatch("getOrder");
      // 跳转到订单页面
      this.$router.push({name:"Order"})
    },
    handleOne(id, name) {
      // 修改商品分类id 和 商品份类名称
      this.$store.commit("getShopid", id);
      this.$store.commit("getFoodName", name);
      this.$store.dispatch("getAllFood");
    },
    showPopup() {
      this.show == true ? (this.show = false) : (this.show = true);
    },
    addNum(id) {
      console.log(id);
      this.$store.commit("addBuyCount", id);

      this.$store.commit("changeCartNum", "add");
    },
    reduceNum(id) {
      this.$store.commit("redBuyCount", id);
      this.$store.commit("changeCartNum", "red");
    },
  },
  computed: {
    foodCategory() {
      return this.$store.state.foodCategory;
    },
    cartNum() {
      return this.$store.state.cartNum;
    },
    cartLIst() {
      return this.$store.state.cartList;
    },
    allPrice() {
      var sum = 0;
      var item = this.$store.state.cartList.list;
      for (let index = 0; index < item.length; index++) {
        sum += item[index].price;
      }
      return sum;
    },
  },
};
</script>
<style>
.mt-Order .van-submit-bar__bar {
  padding: 0 0 0 16px;
  background: #161d26;
  color: #fff;
  position: relative;
}
.mt-Order .van-submit-bar__button {
  height: 100%;
  background: #4fc08d;
  border-radius: 0;
}
.mt-Order .mto-Cart {
  margin-right: 2rem;
}
.mt-Order .mto-Cart .van-button {
  height: 50px;
  width: 50px;
  font-size: 20px;
}
.mt-Order .van-submit-bar__price {
  color: #fff;
}
.mt-Order .van-submit-bar__suffix-label {
  color: #ccc;
}
.mt-Order .dropStyle {
  position: absolute;
  top: -8px;
  left: -5px;
  width: 50px;
  height: 50px;
  opacity: 0.3;
}
.CartTitle {
  font-size: 25px;
  font-weight: bold;
  margin: 2rem;
}
.CartCon {
  padding: 7rem 3rem;
}
</style>