<template>
    <div class="merchantTitle">
      <van-row class="mtrow1">
        <van-col span="3"
          ><router-link to="/"
            ><van-icon name="arrow-left" class="back-icon"
          /></router-link>
        </van-col>
        <van-col offset="6" span="6" style="relative">
          <div class="imgStyle">
            <van-image
              width="70"
              height="70"
              :src="imgUrl+busInfo.photo"
            />
          </div>
        </van-col>
      </van-row>

      <!-- 商家基本信息 -->
      <van-row class="mtrow2">
        <van-tag>品牌</van-tag>
        <span class="mtrTitle">{{busInfo.name}}</span>
        <span><van-icon name="play" /></span>
      </van-row>
      <van-row class="mtrow3">
        <template>
          <span>{{busInfo.score}}</span>
          <span>月售{{busInfo.sales}}单</span>
          <span>硅谷专送</span>
          <span>约{{busInfo.deliveryTime}}分钟</span>
          <span>距离{{busInfo.distance}}米</span>
        </template>
      </van-row>

      <!-- 优惠券单元格 -->
      <van-row class="mtrow4">
        <van-cell @click="show = true">
          <template #title>
            <div class="mtr4-Discount">
              <van-tag color="red">{{special['0'].tag}}</van-tag>
              <!-- 使用JS修改，超过一定长度时显示省略号 -->
              <span class="mtr4-span">{{special['0'].contents}}</span>
              <span>等{{special.length}}个优惠</span>
              <van-icon name="arrow-down" class="mtr4-ico" />
            </div>
          </template>
        </van-cell>
        <van-action-sheet v-model="show" title="优惠活动">
          <div class="content">
            <van-row v-for="(item,index) in special" :key="index">
              <van-tag color="red">{{item.tag}}</van-tag>
              <!-- 使用JS修改，超过一定长度时显示省略号 -->
              <span class="mtr4-span">{{item.contents}}</span>
            </van-row>
          </div>
        </van-action-sheet>
      </van-row>
    </div>
  </div>
</template>
<script>
import { ActionSheet } from "vant";
import { Toast } from "vant";
export default {
  name: "MerchantTitle",
  data() {
    return {
      show: false,
      imgUrl: "http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/",
    };
  },
  components: {
    [ActionSheet.name]: ActionSheet,
  },
  created() {
    this.$store.dispatch("getSpecialOffer");
  },
  methods: {
    onSelect(item) {
      // 默认情况下点击选项时不会自动收起
      // 可以通过 close-on-click-action 属性开启自动收起
      this.show = false;
      Toast(item.name);
    },
  },
  computed: {
    busInfo() {
      return this.$store.state.busInfo;
    },
    special() {
      return this.$store.state.special;
    },
  },
};
</script>

<style>
.merchantTitle .mtrow1 {
  padding: 3rem 0;
  margin-bottom: 10rem;
  background-color: #4fc08d;
}
.merchantTitle .back-icon {
  font-size: 7rem;
  color: #fff;
}
.merchantTitle .imgStyle {
  position: absolute;
  top: 5rem;
  right: calc(50% - 35px);
}
.merchantTitle .imgStyle .van-image {
  padding: 10px;
  box-sizing: border-box;
  background: #fff;
  border-radius: 1rem;
  border: 1px solid #e1e1e1;
}

.mtrow2 .van-tag {
  color: #000;
  background-color: #f0d572;
  margin-right: 3rem;
}
.mtrow2 .mtrTitle {
  font-weight: bold;
  font-size: 4rem;
  margin-right: 5rem;
}
.mtrow3 {
  margin-top: 2rem;
}
.mtrow3 span {
  display: inline-block;
  margin: 0 1rem;
}
.mtrow4 .mtr4-Discount {
  display: inline-block;
  padding: 0 3rem;
  border: 1px solid #e1e1e1;
  border-radius: 2px;
  font-size: 12px;
}
.mtrow4 .mtr4-span {
  margin: 0 2rem;
}
.mtrow4 .mtr4-Discount .mtr4-ico {
  top: 2px;
  margin-left: 2rem;
}
.mtrow4 .content .van-row {
  font-size: 15px;
  text-align: left;
  margin: 2rem 7rem;
}
</style>