<template>
  <!-- 评价组件 -->
  <div class="mt-Evaluate">
    <van-row class="mte-row1">
      <van-col span="10" class="mter1-l">
        <van-row class="mter1-l-tit">{{ busInfo.score }}</van-row>
        <van-row class="mter1-l-zh">综合评分</van-row>
        <van-row class="mter1-l-zb">高于周边商家{{ busInfo.score }}</van-row>
      </van-col>
      <van-col span="14" class="mter1-r">
        <van-row class="mter1r-r1">
          <van-col span="14">
            <van-rate
              v-model="busInfo.serviceScore"
              :size="15"
              color="#ffd21e"
              void-icon="star"
              void-color="#eee"
            />
          </van-col>
          <van-col span="10"
            >服务态度<span style="color: #ffd21e">{{
              busInfo.serviceScore
            }}</span></van-col
          >
        </van-row>
        <van-row class="mter1r-r2">
          <van-col span="14">
            <van-rate
              v-model="busInfo.goodsScore"
              :size="15"
              color="#ffd21e"
              void-icon="star"
              void-color="#eee"
            />
          </van-col>
          <van-col span="10"
            >商品评分<span style="color: #ffd21e">{{
              busInfo.goodsScore
            }}</span></van-col
          >
        </van-row>
        <van-row class="mter1r-r3"
          >送达时间
          <span class="mter1r-r3-min"
            >{{ busInfo.deliveryTime }}分钟</span
          ></van-row
        >
      </van-col>
    </van-row>
    <van-row style="height: 4rem; background: rgb(237 235 235)" />
    <van-row class="mte-row2">
      <van-tabs>
        <div class="tab-btn">
          <van-button icon="success" type="primary" round size="mini" />
          <span class="tab-btn-con">只查看有内容的评价</span>
        </div>

        <van-tab :title="'全部' + busEva.length">
          <div class="talk">
            <div v-for="(item, index) in busEva" :key="index">
              <van-row class="talkrow">
                <van-col span="4">
                  <van-image
                    round
                    width="10rem"
                    height="10rem"
                    src="https://img01.yzcdn.cn/vant/cat.jpeg"
                  />
                </van-col>
                <van-col span="20">
                  <van-row>
                    <van-col span="6">{{ item.account | hide  }}</van-col>
                    <van-col span="10" offset="8">{{ item.oederTime }}</van-col>
                  </van-row>
                  <van-row class="talkrate">
                    <van-rate
                      v-model="item.score / 10"
                      :size="8"
                      color="#ffd21e"
                      void-icon="star"
                      void-color="#eee"
                    />
                  </van-row>
                  <van-row>{{ item.comments }}</van-row>
                </van-col>
              </van-row>
            </div>
          </div>
        </van-tab>
        <van-tab :title="'满意' + isGood">
          <div class="talk">
            <div v-for="(item, index) in goods" :key="index">
              <van-row class="talkrow">
                <van-col span="4">
                  <van-image
                    round
                    width="10rem"
                    height="10rem"
                    src="https://img01.yzcdn.cn/vant/cat.jpeg"
                  />
                </van-col>
                <van-col span="20">
                  <van-row>
                    <van-col span="6">{{ item.account | hide  }}</van-col>
                    <van-col span="10" offset="8">{{ item.oederTime }}</van-col>
                  </van-row>
                  <van-row class="talkrate">
                    <van-rate
                      v-model="item.score / 10"
                      :size="8"
                      color="#ffd21e"
                      void-icon="star"
                      void-color="#eee"
                    />
                  </van-row>
                  <van-row>{{ item.comments }}</van-row>
                </van-col>
              </van-row>
            </div>
          </div>
        </van-tab>

        <van-tab :title="'不满意' + notGood">
          <div class="talk">
            <div v-for="(item, index) in notGoods" :key="index">
              <van-row class="talkrow">
                <van-col span="4">
                  <van-image
                    round
                    width="10rem"
                    height="10rem"
                    src="https://img01.yzcdn.cn/vant/cat.jpeg"
                  />
                </van-col>
                <van-col span="20">
                  <van-row>
                    <van-col span="6">{{ item.account | hide }}</van-col>
                    <van-col span="10" offset="8">{{ item.oederTime }}</van-col>
                  </van-row>
                  <van-row class="talkrate">
                    <van-rate
                      v-model="item.score / 10"
                      :size="8"
                      color="#ffd21e"
                      void-icon="star"
                      void-color="#eee"
                    />
                  </van-row>
                  <van-row>{{ item.comments }}</van-row>
                </van-col>
              </van-row>
            </div>
          </div>
        </van-tab>
      </van-tabs>
    </van-row>
  </div>
</template>
<script>
export default {
  name: "MerchantTabEvaluate",
  data() {
    return {
      isGood: 0,
      goods: [],
      notGood: 0,
      notGoods: [],
    };
  },
  created() {
    this.Good();
  },
  components: {},
  methods: {
    Good() {
      this.$store.dispatch("getBusEva");
      var val = this.$store.state.busEva;
      
      var good = 0;
      var goods = [];
      var notgood = 0;
      var notgoods = [];
      for (var i = 0, j = 0, k = 0; i < val.length; i++) {
        if (val[i].isGood) {
          good++;
          goods.push({
            comments: val[i].comments,
            oederTime: val[i].oederTime,
            score: val[i].score,
            account: val[i].account,
          });
          j++;
        } else {
          notgood++;
          notgoods.push({
            comments: val[i].comments,
            oederTime: val[i].oederTime,
            score: val[i].score,
            account: val[i].account,
          });
          k++;
        }
      }
      this.isGood = good;
      this.goods = goods;
      this.notGood = notgood;
      this.notGoods = notgoods;
    },
  },
  filters: {
    hide: function (val) {
      return val.slice(0, 1) + "xxxxxxxx" + val.slice(val.length - 1);
    },
  },
  computed: {
    busInfo() {
      return this.$store.state.busInfo;
    },
    busEva() {
      return this.$store.state.busEva;
    },
  },
};
</script>
<style>
.mt-Evaluate .mte-row1 {
  padding: 5rem 0;
  border-bottom: 1px solid #ccc;
}
.mt-Evaluate .mte-row1 .mter1-l {
  border-right: 1px solid #e9e4e4;
}
.mt-Evaluate .mte-row1 .mter1-l .mter1-l-tit {
  font-size: 25px;
  color: #ffd21e;
  margin: 3rem 0;
}
.mt-Evaluate .mte-row1 .mter1-l .mter1-l-zh {
  font-weight: bold;
}
.mt-Evaluate .mte-row1 .mter1-l .mter1-l-zb {
  margin: 1rem;
  color: #929191;
}
.mt-Evaluate .mte-row1 .mter1-l,
.mt-Evaluate .mte-row1 .mter1-r {
  height: 28rem;
}
.mt-Evaluate .mte-row1 .mter1-r .mter1r-r1 {
  margin-top: 4rem;
  margin-bottom: 2rem;
}
.mt-Evaluate .mte-row1 .mter1-r .mter1r-r2 {
  margin-bottom: 3rem;
}
.mt-Evaluate .mte-row1 .mter1-r .mter1r-r1,
.mt-Evaluate .mte-row1 .mter1-r .mter1r-r2,
.mt-Evaluate .mte-row1 .mter1-r .mter1r-r3 {
  padding-left: 5rem;
  text-align: left;
}
.mter1r-r3-min {
  color: #ccc;
}
.mte-row2 {
  border-top: 1px solid #ccc;
}
.mte-row2 .van-button--mini {
  width: 20px;
  height: 20px;
  margin-bottom: 4rem;
  margin-left: 5rem;
  margin-top: 5rem;
}
.tab-btn {
  text-align: left;
}
.tab-btn .tab-btn-con {
  top: 1rem;
  left: 2rem;
  position: relative;
}
.talk {
  padding: 3rem 0 2rem 5rem;
  text-align: left;
}
.talk .talkrow {
  padding: 3rem 0;
  border-bottom: 1px solid #eee;
}
.talk .talkrow .talkrate {
  margin: 2rem 0;
}
</style>