import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Login from "../views/Login.vue";
import Search from "../views/Search.vue";
import Mine from "../views/Mine.vue";
import Order from "../views/Order.vue";
import Merchant from '../views/Merchant.vue'
import MTabChild from '../components/MTabChild.vue'

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/Login",
    name: "Login",
    component: Login
  },
  {
    path: "/Search",
    name: "Search",
    component: Search
  },
  {
    path: "/Mine",
    name: "Mine",
    component: Mine
  },
  {
    path: "/Order",
    name: "Order",
    component: Order
  },
  {
    path: "/Merchant",
    name: "Merchant",
    component: Merchant,
    children: [
      {
        path: '/',  //代表 /routerChildren
        name: "Merchant",
        redirect:'/Merchant/MTabChild'
         //当我们访问存在子路由的页面时，重定向为第一个子路由，切记，如果写了component，那么这个component也将显示
      },
      {
        path:"/Merchant/MTabChild/:businessid",
        component:MTabChild
      }
    ],
    props:true
  },
];

const router = new VueRouter({
  // mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
