<template>
  <div class="mtc">
    <!-- 点餐栏的每类商品 -->

    <van-cell>{{ FoodName }}</van-cell>
    <div v-for="(item, index) in allFood" :key="index">
      <van-card
        price="9"
        :title="item.name"
        :desc="item.info"
        :thumb="imgUrl + item.photo"
      >
        <template #num>
          <van-button type="primary" icon="plus" round @click="addCart(item)" />
        </template>
      </van-card>
    </div>
  </div>
</template>
<script>
import { bus } from "./bus";
export default {
  name: "MTabChild",
  data() {
    return {
      imgUrl: "http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/",
      FoodName: "", 
    };
  },
  created() {
    this.getALlFoodVuex();
  },
  components: {},
  methods: {
    getALlFoodVuex() {
      this.FoodName = this.$store.state.foodName;
    },
    addCart(value) {
      console.log(value);
      let flag = this.$store.state.cartList.list.every(
        (item) => item.foodId != value.id
      );
      if (flag) {
        let addItem = {
          foodId: value.id,
          buyCount: 1,
          name: value.name,
          price: 9,
        };
        this.$store.commit("addCartList", addItem);
      } else {
        this.$store.state.cartList.list.forEach((item) => {
          if (item.foodId == value.id) {
            this.$store.commit("addBuyCount", item.foodId);
          }
        });
      }
      this.$store.commit("changeCartNum", "add");
    },
  },
  computed: {
    allFood() {
      this.FoodName = this.$store.state.foodName;
      return this.$store.state.allFood;
    },
  },
};
</script>
<style>
.mtc .van-cell {
  padding: 0 5rem;
  background: #e9e8e8;
  border-left: 2px solid #ccc;
}
.mtc .van-card__content {
  text-align: left;
}
.mtc .van-card__title {
  font-weight: bold;
  line-height: inherit;
  font-size: 16px;
  margin-bottom: 2rem;
}
.mtc .van-card__price {
  color: #ff0000;
}
.mtc .van-button {
  height: 30px;
  width: 30px;
  top: -10px;
}
</style>