<template>
  <div class="Order">
    <van-nav-bar title="我的订单" />
    <!-- 未登录时显示空状态 -->
    <order-log-out v-if="checkLog == false"></order-log-out>
    <!-- 登录成功后的显示状态 -->
    <order-login v-else></order-login>
  </div>
</template>
<script>
import { Empty } from "vant";
import OrderLogOut from "../components/OrderLogOut.vue";
import OrderLogin from "../components/OrderLogin.vue";
export default {
  name: "Order",
  data() {
    return {
      checkLog: false,
    };
  },
  created(){
    this.checkLogin();
  },
  components: {
    [Empty.name]: Empty,
    OrderLogOut,
    OrderLogin,
  },
  methods: {
    checkLogin() {
      var state = localStorage.getItem("account");
      if (state == "" || state == null) {
        this.checkLog = false;
      } else {
        this.checkLog = true;
      }
    },
  },
};
</script>
<style scoped>
.bottom-button {
  width: 160px;
  height: 40px;
  background-color: #4fc08d;
  color: #fff;
}
</style>
