<template>
  <div class="allbus">
    <van-row v-for="(item, index) in allbus" :key="index">
      <router-link :to="'/Merchant/MTabChild/'+item.id" >
        <van-card  :thumb="imageUrl + item.photo">
          <template #title>
            <div style="float: left">
              <van-tag>品牌</van-tag
              ><span class="abtitle">{{ item.name }}</span>
            </div>
            <div style="float: right">
              <van-tag>保</van-tag>
              <van-tag>准</van-tag>
              <van-tag>票</van-tag>
            </div>
          </template>
          <template #desc>
            <div class="abdesc">
              <van-rate v-model="item.score" readonly allow-half />
              <span class="abscore">{{ item.score }}</span>
              <span class="abcount">月销量{{ item.sales }}单</span>
              <van-tag plain>联想教育</van-tag>
            </div>
          </template>
          <template #price>
            ￥{{ item.minPrice }}起送/配送费约￥{{ item.transportationPrice }}
          </template>
        </van-card>
      </router-link>
    </van-row>
  </div>
</template>

<script>
import { Rate } from "vant";
export default {
  name: "AllBussiness",
  data() {
    return {
      imageUrl: "http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/",
      value: 3.7,
    };
  },
  components: {
    [Rate.name]: Rate,
  },
  created() {
    this.getDataVuex();
  },
  methods: {
    getDataVuex() {
      this.$store.dispatch("allbus");
    },
  },
  computed: {
    allbus() {
      return this.$store.state.allbus;
    },
  },
};
</script>

<style>
.allbus .van-card {
  margin: 10px 0;
  border-bottom: 1px solid #eee9e9;
  padding: 8px 8px;
}
.allbus .van-tag {
  color: #000;
  background-color: #f0d572;
  margin: 2px;
}
.allbus .abtitle {
  font-size: 16px;
  font-weight: bold;
  margin-left: 5px;
}
.allbus .abdesc {
  float: left;
  margin-top: 10px;
  width: 100%;
  text-align: left;
}
.abdesc .van-icon {
  font-size: 12px;
}
.abdesc .abscore {
  margin-left: 7px;
  font-size: 16px;
}
.abdesc .abcount {
  margin-left: 7px;
  color: #d8c10a;
}
.abdesc .van-tag--default.van-tag--plain {
  background: none;
  color: #edbb01;
  float: right;
}
.allbus .van-card__bottom {
  text-align: left;
}
.allbus .van-card__price {
  font-size: 14px;
}
</style>


