<template>
  <div class="Mine">
    <van-nav-bar title="我的主页" />

    <van-row class="mrow-one">
      <van-cell size="large" is-link @click="clickUser" border center>
        <template #icon>
          <van-image
            round
            width="17rem"
            height="17rem"
            src="https://img.yzcdn.cn/vant/cat.jpeg"
          />
        </template>
        <template #title>
          <van-row>
            <span>{{ loginTip }}</span>
          </van-row>
          <van-row class="mrodata">
            <van-icon name="phone-o" />
            <span>暂无绑定手机号</span>
          </van-row>
        </template>
      </van-cell>
    </van-row>

    <!-- 是否确认退出弹出框 -->
    <van-popup
      class="popup"
      position="bottom"
      duration="0.4"
      round
      v-model:show="popShow"
    >
      <van-row class="popup-row1"
        ><van-button
          id="bttttt"
          icon="https://cdn.onlinewebfonts.com/svg/img_355990.png"
          plain
          @click="LogOut($event)"
          :loading="loadingState"
        >
        </van-button
      ></van-row>
      <van-row class="popup-row2"><span>退出登录</span></van-row>
      <van-row class="popup-row3"
        ><van-button size="large" @click="popShow = false"
          >取消</van-button
        ></van-row
      >
    </van-popup>
    <!-- 余额优惠积分 -->
    <mine-blance></mine-blance>
    <!-- 单元格列表 -->
    <mine-lists></mine-lists>
  </div>
</template>
<script>
import MineBlance from "../components/MineBlance.vue";
import MineLists from "../components/MineLists.vue";
export default {
  name: "Mine",
  data() {
    return {
      loginTip: "登录 | 注册",
      LoginState: false,
      popShow: false,
      loadingState:true,
    };
  },
  created() {
    var state = localStorage.getItem("account");
    console.log(state);
    if (state == "" || state == null) {
    } else {
      this.loginTip = state;
      this.loginState = true;
    }
  },
  components: { MineBlance, MineLists },
  methods: {
    clickUser() {
      if (this.loginState) {
        this.popShow = true;
        setTimeout(()=>{
          this.loadingState=false;
        },2000)
      } else {
        this.$router.push({ name: "Login" });
      }
    },
    LogOut(i) {
      // 退出登录，刷新界面，销毁loaclstory
      localStorage.removeItem("account");
      this.$router.push({ name: "Home" });
    },
  },
};
</script>
<style scoped>
.Mine .mrow-one .van-cell {
  background-color: #4fc08d;
}
.Mine .van-cell--large {
  height: 100px;
}
.mrow-one {
  margin-bottom: 5rem;
}
.mrow-one .van-cell--large .van-cell__title {
  text-align: left;
  padding-left: 5rem;
  color: #fff;
}
.mrow-one .mrodata {
  font-size: 13px;
}
.mrow-one .van-cell__right-icon {
  color: #fff;
}
.popup {
  height: 200px;
}
.popup .popup-row1 {
  margin-top: 20px;
}
.popup .popup-row1 .van-button--normal {
  border-radius: 10px;
  background: #f2f3f5;
}
.popup .popup-row2 {
  margin: 10px 0 40px 0;
}
.popup .popup-row3 .van-button {
  font-weight: 600;
  width: 90%;
  color: #000;
  background: #f2f3f5;
  border-color: #f2f3f5;
  border-radius: 15px;
}
</style>
