<template>
  <div class="login-cellgroup">
    <van-form @submit="onSubmit">
      <van-field
        v-model="username"
        name="账号"
        placeholder="账号"
        :rules="[{ required: true, message: '请填写账号' }]"
      />
      <van-cell class="login-pwdcell">
        <van-field
          id="pwd"
          v-model="password"
          type="password"
          name="密码"
          placeholder="密码"
          :rules="[{ required: true, message: '请填写密码' }]"
        />
        <template #right-icon>
          <van-switch v-model="checked" size="24" @click="showPwd" />
        </template>
      </van-cell>

      <van-field v-model="text" name="验证码" :placeholder="codePlace">
        <template #right-icon>
          <!-- 验证码 -->
          <canvas id="c3" @click="getChecking"></canvas>
        </template>
      </van-field>

      <div style="margin: 16px">
        <van-button round block type="info" native-type="submit"
          >登录</van-button
        >
      </div>
    </van-form>
  </div>
</template>

<script>
import { Notify } from "vant";
export default {
  name: "loginPwd",
  data() {
    return {
      username: "",
      password: "",
      text: "",
      checked: false,
      isReg: false,
      code: "",
      codePlace:"请输入验证码"
    };
  },
  components: {
    [Notify.name]: Notify,
  },
  created() {
    this.getChecking();
    this.regCode();
  },
  methods: {
    onSubmit(values) {
      if (this.text == this.code) {
        console.log("submit", values);
        var params = {
          account: this.username,
          password: this.password,
        };
        var app = this;
        this.$http.post("/user/login", params).then(function (res) {
          console.log(res.data);
          if (res.data == "") {
            Notify({
              type: "danger",
              message: "用户名或密码错误",
              duration: "2000",
            });
            app.clearFrom();
          } else {
            localStorage.setItem("account", res.data.name);
            localStorage.setItem("userid", res.data.id);
            // 修改vuex中的userid
            app.$store.commit("changeUserid", res.data.id);
            Notify({ type: "success", message: "登陆成功", duration: "2000" });
            setTimeout(() => {
              //1秒后延迟跳转到首页
              app.$router.push({ name: "Home" });
            }, 1000);
          }
        });
      } else {
        this.clearFrom()
        this.codePlace = "验证码错误";
      }
    },
    //显示密码
    showPwd() {
      if (this.checked) {
        document.getElementById("pwd").type = "password";
        this.checked = false;
      } else {
        document.getElementById("pwd").type = "text";
        this.checked = true;
      }
    },
    //获取验证码
    getChecking() {
      var app = this;
      this.$http.get("/user/sendCaptcha").then(function (res) {
        app.checkCode(res.data);
        app.code = res.data
      });
    },
    //登陆失败时清楚输入内容
    clearFrom() {
      this.username = "";
      this.password = "";
      this.text="";
    },
    //验证码样式
    checkCode(code) {
      var str = code;
      //获取画笔
      var w = 120,
        h = 30;
      c3.width = w;
      c3.height = h;

      var ctx = c3.getContext("2d");
      //创建两个函数，返回指定范围内的随机数，随机颜色
      //随机数
      function rn(min, max) {
        var n = Math.random() * (max - min) + min;
        return Math.floor(n);
      }
      //随机颜色
      function rc(min, max) {
        var r = rn(min, max);
        var g = rn(min, max);
        var b = rn(min, max);
        return `rgb(${r},${g},${b})`;
      }
      //填充的背景
      ctx.fillStyle = rc(180, 230);
      ctx.fillRect(0, 0, w, h);
      //创建一个随机的文字[字母和数字]4个

      //console.log(str);
      ctx.font = "30px SimHei";
      ctx.fillStyle = rc(80, 180); //文字颜色
      ctx.textBaseline = "top";
      ctx.fillText(str, 20, 0); //在（20,0）处开始填充文字
      //绘制6条干扰线
      for (var i = 0; i < 6; i++) {
        ctx.strokeStyle = rc(0, 255);
        ctx.beginPath();
        ctx.moveTo(rn(0, w), rn(0, h));
        ctx.lineTo(rn(0, w), rn(0, h));
        ctx.stroke();
      }
      //绘制50个干扰点
      for (var i = 0; i < 50; i++) {
        ctx.fillStyle = rc(0, 255);
        ctx.beginPath();
        ctx.arc(rn(0, w), rn(0, h), 1, 0, 2 * Math.PI);
        ctx.fill();
      }
    },
    //如果是注册的话，将值直接填写到表格
    regCode() {
      console.log(this.$store.state.isReg)
      if (this.$store.state.isReg) {
        this.username = localStorage.getItem("regAcc");
        this.password = localStorage.getItem("regPwd");
        console.log(this.username)
        console.log(this.password)
      }
    },
  },
};
</script>
<style>
.login-cellgroup {
  padding: 5rem;
}
.login-cellgroup .van-cell {
  border: 1px solid #dddddd;
  border-radius: 10px;
  margin: 15px 0;
}
.login-cellgroup .login-pwdcell .van-cell {
  margin: 0;
  border: none;
  padding: 0;
}
.login-cellgroup .van-field__clear,
.login-cellgroup .van-field__right-icon {
  height: 30px;
}
#c3 {
  background: rgb(221, 221, 221);
  width: 120px;
  height: 30px;
}
</style>


