<template>
  <div class="swipe">
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="red" lazy-render>
      <van-swipe-item>
        <van-grid square>
          <van-grid-item
            v-for="(item,index) in category"
            :key="index"
            :icon="imageUrl+item.photo"
            :text="item.name"
          />
        </van-grid>
      </van-swipe-item>
      <van-swipe-item>
        <van-grid square>
          <van-grid-item
            v-for="(item,index) in category"
            :key="index"
            :icon="imageUrl+item.photo"
            :text="item.name"
          />
        </van-grid>
      </van-swipe-item>
    </van-swipe>
   
  </div>
</template>

<script>
import { Swipe, SwipeItem, Grid, GridItem } from "vant";

export default {
  name: "Swipe",
  data() {
    return {
      imageUrl:"http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/",
      category:[]
    };
  },
  components: {
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
    [Grid.name]: Grid,
    [GridItem.name]: GridItem,
  },
  created() {
    this.getBigCategory();
  },
  methods: {
    getBigCategory:function(){
      var app = this;
      this.$http.post("/biz/queryBigCategory").then(function (res) {
        app.category = res.data;
      });
    }
  },
};
</script>

<style scoped>
img {
  width: 100%;
}
.my-swipe .van-swipe-item {
  color: #fff;
  font-size: 20px;
  line-height: 150px;
  text-align: center;
}
</style>


